html {
  min-height: 100%;
}

body {
  color: #fff;
  background-color: #333;
  background-image: linear-gradient(#033536, #010c12);
  min-height: 100%;
  font-family: arial, sans-serif;
}

#debugApp .tick {
  opacity: .1;
}

#debugApp .tick.tickDown {
  color: red;
}

#debugApp .tick.tickUp {
  color: #0f0;
}

#debugApp .tick.tickAnimate {
  animation: .3s forwards blink;
}

@keyframes blink {
  0% {
    opacity: 1;
  }

  50% {
    opacity: .3;
  }

  100% {
    opacity: .2;
  }
}

